.item {
  justify-content: center;
  margin: 135px 0;
  display: flex;
}

.item article {
  width: 50%;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.item__img {
  width: 100%;
}

.item__img img {
  width: 100%;
  border-radius: 15px;
}

.item__content {
  width: 100%;
  color: #fff;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.item__content h1 {
  text-align: left;
  margin-top: 30px;
  margin-bottom: 0;
  font-size: 48px;
  font-weight: 700;
}

.item__content__titlePrice, .item__content__description, .item__content__settings {
  border-bottom: 1px solid #fff;
}

.item__content__titlePrice p {
  margin-top: 4px;
}

.item__content__description {
  flex-direction: column;
  display: flex;
}

.item__content__description__title {
  margin-bottom: 0;
  font-size: 24px;
  font-weight: 600;
}

.item__content__settings {
  padding-top: 24px;
  padding-bottom: 18px;
}

.item__content__settings__color {
  margin-bottom: 10px;
}

.item__content__settings__color select, .item__content__settings__quantity input {
  color: var(--footer-secondary-color);
  text-align: center;
  text-align-last: center;
  border: 1px solid #767676;
  border-radius: 20px;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
}

.item__content__settings__color select:focus, .item__content__settings__quantity input:focus {
  outline: none;
}

.item__content__addButton {
  justify-content: center;
  padding-top: 32px;
  display: flex;
}

.item__content__addButton button {
  background-color: var(--secondary-color);
  color: #fff;
  cursor: pointer;
  border: 0;
  border-radius: 40px;
  padding: 18px 28px;
  font-size: 22px;
}

.item__content__addButton button:hover {
  box-shadow: 0 0 22px 6px #2a12cee6;
}

@media (max-width: 991.98px) {
  .item article {
    width: 70%;
  }

  .item__content__settings__color, .item__content__settings__quantity {
    flex-direction: column;
    align-items: center;
    display: flex;
  }

  .item__content__settings__color {
    margin-bottom: 26px;
  }
}

@media (max-width: 575.98px) {
  .item article {
    width: 90%;
  }
}

